import React, { Component } from 'react';
import { Tabs } from 'antd';
import Powersetting from '../Systemgroup/powersetting'
import Systemlog from '../Systemgroup/Systemlog'
// import Warningmanage from '../Systemgroup/Warningmanage'


class Systemcontrol extends Component {
    state = {
        powerLogin: 0
    }
    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
    }
    render() {

        const { TabPane } = Tabs;
        const bollogin = this.state.powerLogin == 2
        return (
            <div style={{ paddingLeft: "24px", paddingRight: "24px" }}>
                <div style={{ fontSize: "22px", height: '50px', lineHeight: "50px" }}></div>
                <Tabs defaultActiveKey="5">
                    <TabPane tab="" key="1" >
                        <Powersetting />
                    </TabPane>
                    {
                        bollogin && <TabPane tab="" key="2" >
                            <Systemlog />
                        </TabPane>
                    }
                    {/*<TabPane tab="" key="3">
                    </TabPane> 
                    <TabPane tab="" key="3">
                    </TabPane>                  
                    <TabPane tab="" key="5">
                        <Warningmanage/>
                    </TabPane>*/}
                </Tabs>
            </div>
        );
    }
}

export default Systemcontrol;
